<template>
	<view class="container">
		<tabTop left-icon="" title="我的" />
		<view class="contenBox">
			<view class="userName">创世人</view>
			<view class="lanRight" @tap="checkLanguage">
				<label class="chinese" v-if="language"></label>
				<label class="english" v-else></label>
			</view>
			<view class="borderBg">
				<image class="userBg" src="~@/static/me/personal/bg_user.png"></image>
			</view>
			<view class="imgBox">
				<view>
					<!-- <view class="userLeftTxt"> -->
					<view>
						<image class="userLeft" src="~@/static/me/personal/shiziwang.png"></image>

						<view class="levelTxt">
							<image class="levelIcon" src="~@/static/me/personal/level.png"></image>3(霸主)
						</view>
					</view>
					<!-- <label class="levelTxt">(霸主)</label> -->
				</view>
				<!-- </view> -->
				<view class="userHead">
					<image class="userPic" src="~@/static/me/personal/user_pic.png"></image>
				</view>
				<view>
					<image class="userRight" src="~@/static/me/personal/baozi00.png"></image>
					<view class="userRightTxt">狮子王</view>
				</view>
			</view>
			<!-- userName -->
			<view class="fz24 Names">kyumin</view>
			<!-- 设置 -->
			<view class="listBox">
				<view class="itemList" @tap="securityCenter">
					<view class="listLeft">
						<image class="itemListIcon" src="~@/static/me/personal/anquan_icon.png"></image>
						<label class="fz24">安全设置</label>
					</view>
					<view>></view>
				</view>
				<view class="itemList">
					<view class="listLeft">
						<image class="itemListIcon" src="~@/static/me/personal/women_icon.png"></image>
						<label class="fz24">关于我们</label>
					</view>
					<view>></view>
				</view>
				<view class="itemList" @tap="messageCenter">
					<view class="listLeft">
						<image class="itemListIcon" src="~@/static/me/personal/xiaoxi_icon.png"></image>
						<label class="fz24">消息中心</label>
					</view>
					<view>></view>
				</view>
				<view class="itemList" @tap="feedBack">
					<view class="listLeft">
						<image class="itemListIcon" src="~@/static/me/personal/yijian_icon.png"></image>
						<label class="fz24">意见反馈</label>
					</view>
					<view>></view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import tabTop from '@/components/tabTop.vue'
	export default {
		components: {
			tabTop
		},
		data() {
			return {
				language: true
			}
		},
		methods: {
			// 安全设置
			securityCenter() {
				uni.navigateTo({
					url: "SecurityCenter/SecurityCenter"
				})
			},
			// 消息中心
			messageCenter(){
				uni.navigateTo({
					url:'../index/messageCenter/messageCenter'
				})
			},
			// 切换语言
			checkLanguage(){
				let _this = this
				let lan = _this.language
				if(lan){
					_this.language = false
				}else{
					_this.language = true
				}
			},
			// 意见反馈
			feedBack(){
				uni.navigateTo({
					url:'feedBack/feedBack'
				})
			}
		}
	}
</script>
<style>
	page{
		background-image: url(~@/static/bg.png);
		background-size: 100% 100%;
	}
</style>
