	view {
		line-height: 1;
	}

	.fz24 {
		font-size: 24rpx;
	}

	$colTxt:#2DEAB7;

	.container {
		.Names {
			position: absolute;
			top: 358rpx;
			left: 50%;
			transform: translate(-50%, 50%);
			color: $colTxt;
		}
		box-sizing: border-box;
