				height: 176rpx;
				position: absolute;
				// margin: 0 auto;
				top: 0;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}

		.imgBox {
			// width: 100rpx;
			height: 2rpx;
			width: 100%;
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			top: 228rpx;
			// margin: 0 20rpx;
			// box-sizing: border-box;
			text-align: center;

			.leftBox {
