				color: $colTxt;
				font-size: 18rpx;
			}

			.levelTxt {
				font-size: 12rpx;
				color: $colTxt;
			}

			.userRightTxt {
				color: $colTxt;
				font-size: 20rpx;
				height: 22rpx;
			}
		}

		.listBox {
			position: absolute;
			top: 444rpx;
			left: 0;
			right: 0;

			.itemList {
				width: 100%;
				height: 80rpx;
				background: #1E2B49;
				display: flex;
				align-items: center;
				justify-content: space-between;
				color: $colTxt;
				padding: 0 62rpx;
				border-bottom: 2rpx solid #42537A;
				box-sizing: border-box;

				.listLeft {
					display: flex;
